	    task: 'Throughout this tutorial, you\'ll find files named *_explained.txt which are help files that are recommended for all new users, and can be skipped by those more experienced with the command line.\n\nTry using <code>cat ls_explained.txt</code> to read the one about the <code>ls</code> command.',
	    emulator: emulator,
	    checkSolved: utils.lastCommand('cat ls_explained.txt')
